"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { startOfMonth, endOfMonth, eachDayOfInterval, format, getDay, isSameMonth, addDays } from "date-fns";
import { cn } from "@/lib/utils";

// Mock data
const dailyPnL: Record<string, number> = {
    "2023-10-02": 150,
    "2023-10-03": -50,
    "2023-10-04": 300,
    "2023-10-05": 120,
    "2023-10-06": -80,
    "2023-10-09": 450,
    "2023-10-10": 200,
    "2023-10-11": -100,
    "2023-10-12": 500,
    "2023-10-13": 0,
};

export function CalendarHeatmap() {
    const customDate = new Date("2023-10-01"); // Fixed date for demo
    const start = startOfMonth(customDate);
    const end = endOfMonth(customDate);
    const days = eachDayOfInterval({ start, end });

    // Add padding for start of month
    const startDay = getDay(start); // 0 = Sun
    const paddedDays = Array.from({ length: startDay === 0 ? 6 : startDay - 1 }).map((_, i) => null).concat(days as any[]);

    return (
        <Card className="col-span-3">
            <CardHeader>
                <CardTitle>October 2023 Performance</CardTitle>
            </CardHeader>
            <CardContent>
                <div className="grid grid-cols-7 gap-2 text-center text-xs text-muted-foreground mb-2">
                    <div>Mon</div><div>Tue</div><div>Wed</div><div>Thu</div><div>Fri</div><div>Sat</div><div>Sun</div>
                </div>
                <div className="grid grid-cols-7 gap-2">
                    {paddedDays.map((day, i) => {
                        if (!day) return <div key={`pad-${i}`} className="aspect-square" />;

                        const dateStr = format(day, "yyyy-MM-dd");
                        const pnl = dailyPnL[dateStr];
                        let bgClass = "bg-secondary";

                        if (pnl > 0) bgClass = "bg-green-500/20 text-green-500 border-green-500/50";
                        else if (pnl < 0) bgClass = "bg-red-500/20 text-red-500 border-red-500/50";
                        else if (pnl === 0) bgClass = "bg-secondary text-muted-foreground";

                        return (
                            <div
                                key={dateStr}
                                className={cn(
                                    "aspect-square flex flex-col items-center justify-center rounded-md border border-transparent transition-all hover:scale-105 cursor-pointer",
                                    bgClass
                                )}
                            >
                                <div className="text-[10px] font-bold">{format(day, "d")}</div>
                                {pnl !== undefined && (
                                    <div className="text-[10px] font-semibold">
                                        {pnl > 0 ? "+" : ""}{pnl}
                                    </div>
                                )}
                            </div>
                        );
                    })}
                </div>
            </CardContent>
        </Card>
    );
}
